@font-face {
  font-family: "montserrat";
  src: url("../fonts/montserrat-regular.woff2") format("woff2"), url("../fonts/montserrat-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "montserrat";
  src: url("../fonts/montserrat-medium.woff2") format("woff2"), url("../fonts/montserrat-medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "montserrat";
  src: url("../fonts/montserrat-semibold.woff2") format("woff2"), url("../fonts/montserrat-semibold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "montserrat";
  src: url("../fonts/montserrat-bold.woff2") format("woff2"), url("../fonts/montserrat-bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  font-family: "Montserrat", "Verdana", sans-serif;
  color: #011c40;
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #f9fbfd;
}

a {
  color: #000000;
  text-decoration: none;
}

h1 {
  margin: 0;
}

h2 {
  margin: 0;
}

h3 {
  margin: 0;
}

h4 {
  margin: 0;
}

p {
  margin: 0;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #f9fbfd;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email], [type=number], [type=password], [type=search], [type=tel], [type=text], [type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.reset-list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 40px;
}
@media (max-width: 1439px) {
  .container {
    padding: 0 32px;
  }
}
@media (max-width: 1023px) {
  .container {
    padding: 0 24px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 16px;
  }
}

.header {
  position: absolute;
  width: 100%;
  display: flex;
  z-index: 1;
  justify-content: center;
}

.header__container {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  max-width: 1366px;
  min-height: 100px;
  align-items: center;
  justify-content: space-between;
  padding-left: 103px;
  padding-right: 81px;
}
@media (max-width: 1023px) {
  .header__container {
    padding-left: 45px;
    padding-right: 32px;
    min-height: 80px;
  }
}
@media (max-width: 767px) {
  .header__container {
    padding: 0 15px;
    min-height: 61px;
    position: relative;
  }
}

@media (max-width: 767px) {
  .header__container--opened {
    background-color: #f9fbfd;
    flex-direction: column;
    align-content: flex-start;
    justify-content: flex-start;
    min-height: fill-available;
    width: 89%;
    position: fixed;
    right: 0;
  }
}

.header__logo {
  margin-top: 10px;
}
@media (max-width: 1023px) {
  .header__logo {
    width: 38px;
    height: 20px;
    margin-top: 0;
  }
}
@media (max-width: 767px) {
  .header__logo {
    width: 42px;
    height: 22px;
  }
}

.header__logo-link:hover {
  opacity: 0.8;
}
.header__logo-link:focus {
  opacity: 0.8;
}
.header__logo-link:active {
  opacity: 0.8;
}
.header__logo-link:focus-visible {
  outline: none;
}

.header__logo-svg {
  fill: #f9fbfd;
}
@media (max-width: 1023px) {
  .header__logo-svg {
    width: 38px;
    height: 20px;
  }
}
@media (max-width: 767px) {
  .header__logo-svg {
    width: 42px;
    height: 22px;
  }
}

@media (max-width: 767px) {
  .header__logo-svg--opened {
    position: absolute;
    left: 15px;
    top: 20px;
    width: 42px;
    height: 22px;
  }
}

.header__nav-list {
  display: flex;
  width: 100%;
  margin-top: 10px;
  color: #f9fbfd;
}
@media (max-width: 1023px) {
  .header__nav-list {
    margin-top: 0;
  }
}
@media (max-width: 767px) {
  .header__nav-list {
    display: none;
  }
}

@media (max-width: 767px) {
  .header__nav-list--opened {
    display: flex;
    flex-direction: column;
    margin-top: 53px;
    color: #011c40;
  }
}

.header__nav-item {
  padding: 10px;
  margin-right: 13px;
}
@media (max-width: 1023px) {
  .header__nav-item {
    margin-right: 4px;
  }
}
@media (max-width: 767px) {
  .header__nav-item {
    margin-bottom: 6px;
  }
}

.header__nav-link {
  font-family: "Montserrat", "Verdana", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  color: #f9fbfd;
}
@media (max-width: 1023px) {
  .header__nav-link {
    font-family: "Montserrat", "Verdana", sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    color: #f9fbfd;
  }
}
@media (max-width: 767px) {
  .header__nav-link {
    color: inherit;
    font-size: 18px;
    line-height: 22px;
  }
}
.header__nav-link:hover {
  text-decoration: revert;
  opacity: 0.8;
}
.header__nav-link:focus {
  text-decoration: revert;
}
.header__nav-link:active {
  color: #011c40;
}
.header__nav-link:focus-visible {
  outline: none;
}

.header__mobile {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}
@media (max-width: 767px) {
  .header__mobile {
    width: auto;
    height: auto;
    margin: 0;
    overflow: auto;
    clip-path: none;
    clip: auto;
    right: 0;
  }
}

.header__toggle-button {
  background-color: transparent;
  padding: 10px;
  border: none;
  display: none;
}
@media (max-width: 767px) {
  .header__toggle-button {
    display: block;
    margin-right: 5px;
  }
}

.header__toggle-button--opened {
  display: none;
}

.header__cross-button {
  background-color: transparent;
  padding: 10px;
  border: none;
  display: none;
}

.header__cross-button--opened {
  display: block;
  position: absolute;
  top: 14px;
  right: 5px;
}

