.form__control--checkbox label {
  position: relative;
  display: block;
  padding: 23px 0 32px 21px;
  cursor: pointer;
}
.form__control--checkbox label::before {
  position: absolute;
  top: 20px;
  left: -3px;
  content: "";
  width: 18px;
  height: 18px;
  background-color: #f9fbfd;
}
